















































import Vue from 'vue'
import { register } from '@/api/auth'

export default Vue.extend({
  name: 'Login',
  data () {
    return {
      user: {
        username: '',
        email: '',
        password: ''
      }
    }
  },
  methods: {
    async onSubmit () {
      try {
        // 
        // 
        // 
        const data = await register({
          user: this.user
        })
        console.log(data.user.username)
        // 
        this.$router.push({
          name: 'home'
        })
      } catch (err) {
        console.log('...', err)
      }
    }
  }
})
