// 
import request from './request'

//  
// export
export interface User {
  user: {
    username: string
    email: string
    password: string
  }
}

//  
export interface RegisterData {
  user: {
    email: string
    token: string
    username: string
    bio: string
    image: string
  }
}

// 
export const register = async (data: User): Promise<RegisterData> => { // asyncPromise
  const res = await request({
    method: 'POST',
    url: '/users',
    data
  })
  console.log(res.data)
  return res.data
}

